<!-- src/views/Login.vue -->
<template>
  <div class="row justify-content-center">
    <div class="col-md-6">
      <div class="card shadow-sm">
        <div class="card-body">
          <h5 class="card-title">Login</h5>
          <form @submit.prevent="onSubmit">
            <input v-model="email" type="email" class="form-control mb-2" placeholder="Email" required />
            <input v-model="password" type="password" class="form-control mb-2" placeholder="Password" required />
            <div class="d-flex justify-content-between">
              <button class="btn btn-primary" :disabled="loading">{{ loading ? 'Logging...' : 'Login' }}</button>
              <router-link to="/register">Register</router-link>
            </div>
          </form>
          <div v-if="error" class="alert alert-danger mt-3">{{ error }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import { useRouter, useRoute } from 'vue-router';

import auth from '@/services/auth';

const email = ref('');
const password = ref('');
const error = ref('');
const loading = ref(false);

const router = useRouter();
const route = useRoute();

async function onSubmit() {
  error.value = '';
  loading.value = true;
  try {
    // debug: uncomment to verify import
    // console.log('auth:', auth);

    await auth.login(email.value, password.value);
    const redirect = route.query.redirect || '/';
    router.push(redirect);
  } catch (err) {
    error.value = err.response?.data?.error || err.message || 'Login failed';
  } finally {
    loading.value = false;
  }
}
</script>